<template>
  <page
    @on-ready="onReady"
    ref="page"
    class="sign-page common-page"
    :headerHeightVW="headerHeightVW"
  >
    <van-nav-bar slot="header" id="header" title="拍照签到" left-arrow @click-left="onClickLeft">
    </van-nav-bar>

    <van-cell-group>
      <van-cell title="请拍照后签到:"></van-cell>
      <div class="images-container">

        <div class="image-item-show" v-for="(item, index) in imageList" :key="index" @click="handlePlayImages(index)">
          <!-- <img :src="item.url" @click="handlePlayImages(index)"> -->
          <auto-image :src="item.url" :width="108" :height="108" style="margin-bottom: 12px;margin-left: 12px;"></auto-image>
          <div class="image-remove" @click.stop="handleImageRemove(index)"></div>
        </div>
        <div class="image-item" style="background: #fff;"  @click.stop="handlePhoto1">
          <img src="../../assets/images/image-add.png">
        </div>
      </div>
    </van-cell-group>

    <div class="blank-empty"></div>

    <template slot="blank">
      <div class="footer-action">
        <van-button v-if="imageList.length == 0" class="action-item-main" color="#989BA2" :disabled="true">确认签到</van-button>
        <van-button @click="handleSign" v-if="imageList.length > 0" class="action-item-main" color="#108EE9">确认签到</van-button>
      </div>
    </template>
  </page>
</template>

<script>
import Page from '../../components/Page'
import navigate from '../../utils/navigate.js'
import db from '../../db'
import moment from 'moment'
import AutoImage from '../../components/AutoImage'

export default {
  components: {
    Page,
    AutoImage
  },
  data: () => ({
    headerHeightVW: 0,
    imageList: [],
    photoList: [],
    imageFilter: null,
    orderid: null,
  }),
  methods: {
    onReady (header, main) {
      this.header = header

      this.headerHeightVW = (this.header.h + 1) * 100 / this.header.w

      this.orderid = navigate.getParam('orderid')
    },
    onClickLeft() {
      navigate.back()
    },

    handlePhoto1 () {
      api.actionSheet({
        buttons: ['打开照相机', '从相册获取']
      }, (ret, err) => {
        var index = ret.buttonIndex;
        let sourceType = ''
        if (index === 1) {
          sourceType = 'camera'
        } else if (index === 2) {
          sourceType = 'album'
        }

        sourceType && api.getPicture({
          sourceType: sourceType,
          mediaValue: 'pic',
          destinationType: 'url',
          preview: true,
          quality: 50,
        }, (ret, err) => {
          if (ret.data) {

            this.imageList.push({url: ret.data})
          }
        })
      })
    },

    handlePlayImages (index) {
      localStorage.setItem('photos', JSON.stringify(this.imageList))
      navigate.open('photoViewer', {index: index})
    },

    handleImageRemove (index) {
      this.imageList.splice(index, 1)
    },

    handleSign () {
      // 获取工单详情
      this.$toast.loading({
        duration: 0,
        forbidClick: true,
        loadingType: 'spinner',
        message: '正在签到...'
      })

      db.getOrderDetail(this.orderid).then(data => {
        if (this.orderid == data.id) {
          
          // 添加签到信息
          const signData = {
            signType: 1,
            signPhoto: this.imageList.map(item => item.url),
            signTime: moment().format('YYYY-MM-DD HH:mm'),
            status: 'waiting_repair'
          }

          db.updateOrder({
            ...data,
            ...signData
          }).then(() => {
            navigate.back()
          })
        } else {
          this.$toast('签到失败')
        }
      })
    }
  }
}
</script>

